html,
body {
	font-size: 14px;
	padding: 0;
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen,
		Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}

* {
	box-sizing: border-box;
	word-break: break-word;
}

img {
	max-width: 100%;
}

main {
	flex: 1;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
	position: absolute;
}

.clickable,
a {
	color: inherit;
	text-decoration: none;
	color: #940d0d;
	text-decoration: underline;
}

.title {
	margin: 0;
	line-height: 1.15;
	font-size: 4rem;
	a {
		color: #0070f3;
		text-decoration: none;
		&:hover,
		&:focus,
		&:active {
			text-decoration: underline;
		}
	}
}

.title,
.description {
	font-size: 0px;
	text-align: center;
}

code {
	background: #fafafa;
	border-radius: 5px;
	padding: 0.75rem;
	font-family: Menlo, Monaco, Lucida Console, Liberation Mono,
		DejaVu Sans Mono, Bitstream Vera Sans Mono, Courier New, monospace;
}

.card {
	margin: 1rem;
	flex-basis: 45%;
	padding: 1.5rem;
	text-align: left;
	color: inherit;
	text-decoration: none;
	border: 1px solid #eaeaea;
	border-radius: 10px;
	transition: color 0.15s ease, border-color 0.15s ease;
	&:hover,
	&:focus,
	&:active {
		color: #0070f3;
		border-color: #0070f3;
	}
	h3 {
		margin: 0 0 1rem 0;
		font-size: 1.5rem;
	}
	p {
		margin: 0;
		font-size: 1.25rem;
		line-height: 1.5;
	}
}

footer {
	position: absolute;
	bottom: 0;
	width: 100%;
	padding: 5px 0;
	font-size: 10px;
	border-top: 1px solid #eaeaea;
	color: silver;
	img {
		margin: 0;
		margin-right: 0.1rem;
	}
}

button {
	font-size: 15px;
	border: 0;
	background: #ed1813;
	color: white;
	padding: 5px 10px;
	font-weight: 300;
	width: 100%;
	cursor: pointer;
}

.mainBlock {
	width: 20%;
	height: 100%;
	border-left: 1px solid #eaeaea;
	display: flex;
	justify-content: flex-start;
	align-items: flex-start;
	position: fixed;
	flex-direction: column;
	top: 0;
	right: 0;
	z-index: 1;
	background: white;
	overflow: hidden;
	.block {
		width: 100%;
		&.activityArea {
			height: 100%;
			position: relative;
		}
		&.makeMark {
			text-align: center;
			border-bottom: 1px solid #eaeaea;
			padding: 10px 20px;
		}
	}
	img {
		margin-left: 0.5rem;
	}
	a {
		display: flex;
		justify-content: center;
		align-items: center;
	}
}

.popup {
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	z-index: 1;
	display: flex;
	align-items: center;
	justify-content: center;
	background: rgba(0, 0, 0, 0.4);
}

.mapArea {
	width: 80%;
	height: 100%;
	position: fixed;
	left: 0;
	top: 0;
}

.form {
	width: 350px;
	min-height: 500px;
	background: white;
	padding: 20px;
	font-size: 15px;
	max-height: 700px;
	overflow: hidden;
	overflow-y: auto;
	h2 {
		text-align: center;
		margin-top: 0;
	}
	button {
		font-size: 20px;
		padding: 15px 20px;
	}
}

textarea,
input {
	width: 100%;
	max-width: 100%;
	padding: 10px;
	border: 1px solid #dadada;
	margin-bottom: 10px;
}

select,
textarea,
input {
	font-size: 15px;
}

ul,
ol {
	list-style: none;
	padding: 0;
	margin: 0;
}

ul {
	color: dimgrey;
	margin-bottom: 20px;
	li {
		margin-bottom: 5px;
	}
}

textarea:required,
input:required {
	border-color: #e99090;
}

.activityArea {
	ol {
		display: table;
		table-layout: fixed;
		width: 100%;
		border-bottom: 1px solid #eaeaea;
		li {
			text-align: center;
			border-right: 1px solid #eaeaea;
			padding: 7px 5px;
			cursor: pointer;
			display: table-cell;
			vertical-align: middle;
			height: 40px;
			position: relative;
			&.act {
				object,
				svg,
				img {
					margin: 0;
					width: 25px;
				}
			}
			svg {
				.path1 {
					fill: #d8d8d8;
				}
			}
			&:first-child {
				width: 40px;
				background: #efefef;
			}
			&:last-child {
				border: 0;
			}
			&.active:after {
				content: "";
				position: absolute;
				width: 100%;
				bottom: 0;
				left: 0;
				border-bottom: 2px solid #ed1812;
			}
		}
	}
	&.news .activity {
		margin-left: -100%;
	}
	&.acts .activity {
		margin-left: 0;
	}
	&.local {
		ol li.active:after {
			display: none;
		}
		svg {
			.path1 {
				fill: #ed1812;
			}
		}
		.activity {
			margin-left: 100%;
		}
	}
	.activity {
		transition: all 0.5s;
		.bb {
			padding: 10px;
			transition: all 0.5s;
			position: absolute;
			width: 100%;
			height: 100%;
			padding-bottom: 200px;
			overflow-y: auto;
		}
		.b2 {
			margin-left: -100%;
		}
		.b3 {
			margin-left: 100%;
		}
		.author {
			padding: 10px;
			border: 1px solid #eaeaea;
			background: #f7f7f7;
			margin-bottom: 15px;
			h3 {
				margin: 0;
				margin-bottom: 10px;
				&.clickable {
					text-decoration: underline;
				}
			}
			.src {
				margin-bottom: 5px;
				margin-top: 5px;
			}
			iframe {
				width: 100%;
				height: 100%;
			}
		}
		.comments {
			h3 {
				margin-bottom: 10px;
			}
			.cm {
				margin-bottom: 8px;
				.chead {
					display: flex;
					align-items: center;
					margin-bottom: 4px;
					span {
						font-size: 10px;
						color: #929292;
						padding-left: 3px;
					}
				}
			}
		}
	}

	.imgb {
		img {
			width: 35%;
			float: left;
			margin-right: 10px;
		}
	}
}

.subscribe {
	padding: 0 20px;
	position: absolute;
	bottom: 0;
	height: 140px;
	background: #2e2e2e;
	color: white;
	h2 {
		font-size: 15px;
		text-align: center;
	}
	img {
		margin: 0;
		cursor: pointer;
		width: 30px;
		padding: 5px;
		border: 1px solid #ffffff;
		border-radius: 50%;
		margin-right: 5px;
		&.selected {
			border: 1px solid #ff0700;
		}
	}
	.sel {
		display: flex;
		align-items: center;
	}
	.inpf {
		display: flex;
		align-items: center;
		&.phone {
			select,
			input {
				height: 30px;
			}
			select {
				width: 60px;
				margin-right: 3%;
			}
			input {
				width: 70%;
				margin-right: 3%;
			}
		}
		&.email {
			input {
				margin-right: 3%;
			}
		}
		input,
		select {
			border-color: white;
			background: none;
			color: white;
		}
		input {
			border-radius: 5px;
			margin: 0;
			padding: 6px 10px;
		}
	}
	button {
		width: 50px;
		border-radius: 5px;
		padding: 6px 0;
		svg {
			width: 13px;
		}
	}
}

.socials {
	position: fixed;
	margin: auto;
	margin-left: -3%;
	text-shadow: 0 0 black;
	top: 0;
	bottom: 0;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	.share-links {
		a {
			border: 1px solid #d4d4d4;
			padding: 5px;
			border-radius: 50%;
			height: 35px;
			width: 35px;
			text-align: center;
			margin-bottom: 5px;
			background: #515151;
			svg {
				width: 17px;
				font-size: 20px;
				color: #fdfdfd;
			}
		}
	}
}

.skeleton {
	display: table;
	width: 100%;
	.skeleton-content {
		display: table-cell;
		width: 100%;
		vertical-align: top;
	}
	.skeleton-content {
		.skeleton-title {
			width: 100%;
			height: 16px;
			margin-top: 16px;
			background: #f2f2f2;
			margin: 0;
			padding: 0;
			+ .skeleton-paragraph {
				margin-top: 24px;
			}
		}
		.skeleton-paragraph {
			padding: 0;
			list-style: none;
			> li {
				width: 100%;
				height: 16px;
				list-style: none;
				background: #f2f2f2;
				background: -webkit-gradient(
					linear,
					left top,
					right top,
					color-stop(25%, #f2f2f2),
					color-stop(37%, #e6e6e6),
					color-stop(63%, #f2f2f2)
				);
				background: linear-gradient(
					90deg,
					#f2f2f2 25%,
					#e6e6e6 37%,
					#f2f2f2 63%
				);
				background-size: 400% 100%;
				-webkit-animation: skeleton-loading 1.4s ease infinite;
				animation: skeleton-loading 1.4s ease infinite;
			}
		}
	}
}

@keyframes skeleton-loading {
	0% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0 50%;
	}
}

@media (max-width: 600px) {
}
